import {
    Button,
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Tab,
    Tabs,
    } from "react-bootstrap";
    import { baseUrl } from "../../../../../utils";
    import React, { Suspense } from "react";
import url from "socket.io-client/lib/url";
import NoData from "../../layout/common/NoData";
import { NoCameraImgPost } from "../../../../../actions/api";
    const SentimentpostSkeleton = React.lazy(() =>
        import("../../layout/skeleton/SentimentpostSkeleton")
      );

    const SentimentPost = (props) => {

      const { loader } = props  
      const { negative, positive } = props && Object.keys(props).length ? props[1] : [];

        return (
            <Card className="sw-card-custom gutter-b p-5 card-shadow">
            <CardHeader className="p-0 border-0">
                <CardTitle className="mb-3 d-flex align-items-center justify-content-between">
                    <h6 class="card-label font-weight-bolder">
                    <img
                    src={baseUrl(`dashboard/analytic/ai.svg`)}
                    height={"20px"}
                    width={"20px"}
                    />{" "}
                    Sentiment
                </h6>
                <Button variant="link" className="p-0">See More</Button>
                </CardTitle>
            </CardHeader>
            <CardBody className="p-0">
                <Tabs
                defaultActiveKey="positive"
                id="uncontrolled-tab-example"
                className="nav-tabs-line sw-custom-nav nav nav-tabs mb-3"
                >
                <Tab
                    eventKey="positive"
                    title={
                    <div className="px-2">
                        <img
                        src={baseUrl(`dashboard/analytic/smile-active.svg`)}
                        height={"16px"}
                        width={"16px"}
                        />{" "}
                        positive
                    </div>
                    }
                >
                 { loader ? 
                    <Suspense fallback={<></>}>
                        <SentimentpostSkeleton/>
                    </Suspense>
                   : positive &&
                    Object.keys(positive).length
                    ? positive.map((data) => {
                        return (
                            <div className="px-0 py-3 cursor-pointer border-bottom">
                                <div className="d-flex align-items-center">
                                    <div className="symbol_item position-relative">
                                        <div className="symbol symbol-100 symbol-2by2 flex-shrink-0 me-4">
                                            <Button type="button" className="border-0 p-0 bg-transparent rounded">
                                                <span className="symbol-label no-repeat-fit-background rounded" style={{backgroundImage: data.postFile ? data.postFile : NoCameraImgPost}}>
                                                    <img src={data.postFile} className="img-fluid" onError={e => e.target.src = NoCameraImgPost} />
                                                </span>
                                            </Button>
                                        </div>
                                    </div>
                                    <div className="w-100 me-10">
                                    <div className="d-flex flex-grow-1 w-100 mb-2 justify-content-between pe-10">
                                        <div className="d-flex align-items-center w-50">
                                        <div className="symbol symbol-35 symbol-light me-3 con_user_img cursor-pointer rounded-circle overflow-hidden">
                                            <img src={data.picture} className="img-fluid" width={"20px"} height={"20px"} />
                                        </div>
                                        <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
                                            <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                                            {data.username}
                                            </p>
                                            <p class="m-0">
                                            <small class="text-muted overflow-hidden text-nowrap text-ellipsis post_count me-2 ">
                                                1 Post
                                            </small>
                                            <small class="text-muted overflow-hidden text-nowrap text-ellipsis followers_count ">
                                                31K Followers
                                            </small>
                                            </p>
                                        </div>
                                        </div>
                                        <div class="list_category_icon cursor-pointer pt-1">
                                            <i className={`fab ${data.networkIcon} font-size-h2`}></i>
                                        </div>
                                    </div>
                                    <div className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3">
                                        {data.content}
                                    </div>
                                    </div>
                                    <div className="conn_status_wrap">
                                    <Button  onClick={() =>window.open(data.postLink)}
                                    className="btn-min-w btn-sm rounded">
                                        <span class="position-relative">
                                        {" "}
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="22.213"
                                            height="18"
                                            viewBox="0 0 22.213 18"
                                            class="h-75 align-self-end w-20 me-1"
                                        >
                                            <g
                                            id="noun-talk-2902544"
                                            transform="translate(-98.007 -75.786)"
                                            >
                                            <path
                                                id="Path_6701"
                                                data-name="Path 6701"
                                                d="M103.686,84.417a1.084,1.084,0,1,1-.317-.766A1.084,1.084,0,0,1,103.686,84.417Zm2.9-1.084h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm3.979,0h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm9.659-5.711v8.172a1.839,1.839,0,0,1-1.616,1.823l.625,2.7a.617.617,0,0,1-1,.608l-3.07-2.625v.2a1.838,1.838,0,0,1-1.836,1.836h-9.463L100,93.638a.617.617,0,0,1-1-.608l.625-2.7A1.838,1.838,0,0,1,98.007,88.5V80.331a1.838,1.838,0,0,1,1.836-1.836h3.226v-.873a1.838,1.838,0,0,1,1.836-1.836h13.478a1.838,1.838,0,0,1,1.836,1.836Zm-6.3,2.709a.6.6,0,0,0-.6-.6H99.843a.6.6,0,0,0-.6.6V88.5a.6.6,0,0,0,.6.6h.552a.617.617,0,0,1,.6.756l-.375,1.621,2.607-2.229a.618.618,0,0,1,.4-.148h9.691a.6.6,0,0,0,.6-.6Zm5.063-2.709a.6.6,0,0,0-.6-.6H104.906a.6.6,0,0,0-.6.6V78.5h9.017a1.838,1.838,0,0,1,1.836,1.836v6.35l2.448,2.093-.375-1.621a.617.617,0,0,1,.6-.756h.553a.6.6,0,0,0,.6-.6Z"
                                                fill="#ffffff"
                                            ></path>
                                            </g>
                                        </svg>{" "}
                                        Connect 
                                        </span>
                                    </Button>
                                    </div>
                                </div>
                            </div>
                        );
                        })
                    : <NoData/> } 
                </Tab>
                <Tab
                    eventKey="negative"
                    title={
                    <div className="px-2">
                        <img
                        src={baseUrl(`dashboard/analytic/sad.svg`)}
                        height={"16px"}
                        width={"16px"}
                        />{" "}
                        Negative
                    </div>
                    }
                >
                  { 
                   loader ? 
                    <Suspense fallback={<></>}>
                        <SentimentpostSkeleton/>
                    </Suspense>
                   :
                   negative &&
                    Object.keys(negative).length
                    ? negative.map((data) => {
                        return (
                            <div className="px-0 py-3 cursor-pointer border-bottom">
                            <div className="d-flex align-items-center">
                                <div className="symbol_item position-relative">
                                <div className="symbol symbol-100 symbol-2by2 flex-shrink-0 me-4">
                                    <div className="rounded">
                                        <img src={data.postFile} className="img-fluid" width={"80px"} height={"80px"} />
                                    </div>
                                </div>
                                </div>
                                <div className="w-100 me-10">
                                <div className="d-flex flex-grow-1 w-100 mb-2 justify-content-between pe-10">
                                    <div className="d-flex align-items-center w-50">
                                    <div className="symbol symbol-45 symbol-light me-3 con_user_img cursor-pointer">
                                        <img src={data.picture} className="img-fluid" width={"20px"} height={"20px"} />
                                    </div>
                                    <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
                                        <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name">
                                         {data.username}
                                        </p>
                                        <p class="m-0">
                                        <small class="text-muted overflow-hidden text-nowrap text-ellipsis post_count me-2 ">
                                            1 Post
                                        </small>
                                        <small class="text-muted overflow-hidden text-nowrap text-ellipsis followers_count ">
                                            31K Followers
                                        </small>
                                        </p>
                                    </div>
                                    </div>
                                    <div class="list_category_icon cursor-pointer pt-1">
                                         <i className={`fab ${data.networkIcon}`}></i>
                                    </div>
                                </div>
                                <div className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3">
                                     {data.content}
                                </div>
                                </div>
                                <div className="conn_status_wrap">
                                <Button  onClick={() =>window.open(data.postLink)}
                                 className="btn-min-w btn-sm rounded">
                                    <span class="position-relative">
                                    {" "}
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="22.213"
                                        height="18"
                                        viewBox="0 0 22.213 18"
                                        class="h-75 align-self-end w-20 me-1"
                                    >
                                        <g
                                        id="noun-talk-2902544"
                                        transform="translate(-98.007 -75.786)"
                                        >
                                        <path
                                            id="Path_6701"
                                            data-name="Path 6701"
                                            d="M103.686,84.417a1.084,1.084,0,1,1-.317-.766A1.084,1.084,0,0,1,103.686,84.417Zm2.9-1.084h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm3.979,0h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm9.659-5.711v8.172a1.839,1.839,0,0,1-1.616,1.823l.625,2.7a.617.617,0,0,1-1,.608l-3.07-2.625v.2a1.838,1.838,0,0,1-1.836,1.836h-9.463L100,93.638a.617.617,0,0,1-1-.608l.625-2.7A1.838,1.838,0,0,1,98.007,88.5V80.331a1.838,1.838,0,0,1,1.836-1.836h3.226v-.873a1.838,1.838,0,0,1,1.836-1.836h13.478a1.838,1.838,0,0,1,1.836,1.836Zm-6.3,2.709a.6.6,0,0,0-.6-.6H99.843a.6.6,0,0,0-.6.6V88.5a.6.6,0,0,0,.6.6h.552a.617.617,0,0,1,.6.756l-.375,1.621,2.607-2.229a.618.618,0,0,1,.4-.148h9.691a.6.6,0,0,0,.6-.6Zm5.063-2.709a.6.6,0,0,0-.6-.6H104.906a.6.6,0,0,0-.6.6V78.5h9.017a1.838,1.838,0,0,1,1.836,1.836v6.35l2.448,2.093-.375-1.621a.617.617,0,0,1,.6-.756h.553a.6.6,0,0,0,.6-.6Z"
                                            fill="#ffffff"
                                        ></path>
                                        </g>
                                    </svg>{" "}
                                    Connect 
                                    </span>
                                </Button>
                                </div>
                            </div>
                            </div>
                        );
                        })
                      : 
                       <NoData/>
                    }
                </Tab>
                </Tabs>
            </CardBody>
            </Card>
          );
        };
    

 export default React.memo(SentimentPost);